const state = {
    author: 'er_567'
}

// getters
const getters = {}

// actions
const actions = {}

// mutations
const mutations = {
    editAuthor(state,name){
        state.author = name;
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}



